.elixirListWrapper{position:absolute;inset:0;display:flex;align-items:center;justify-content:center;z-index:100;background:rgba(8,8,8,.5)}.elixirList{display:flex;flex-direction:column;width:800px;height:500px}.elixirList .titleBar{background:rgba(20,20,20,.95);font-size:20px;display:flex;flex-direction:row;padding:6px 12px}.elixirList .titleBar .title{flex-grow:1}.elixirList .titleBar .close{cursor:pointer;background:#000;width:24px;height:24px;margin-right:-4px;font-size:32px;display:flex;align-items:center;justify-content:center;color:#fff}.elixirList .titleBar .close::before{content:"×"}.elixirList .body{background:rgba(10,10,10,.95);flex:1 1;flex-basis:0;min-height:0;display:flex;flex-direction:row}.elixirList .body .itemList{flex-grow:1}.elixirList .body .effectList{flex-grow:1.5;border-left:4px solid rgba(20,20,20,.95);border-right:4px solid rgba(20,20,20,.95)}.elixirList .body .itemList,.elixirList .body .effectList{list-style-type:none;padding:10px;margin:0;flex-basis:0}.elixirList .body .itemList li,.elixirList .body .effectList li{padding:4px 0;cursor:pointer}.elixirList .body .itemList li.active,.elixirList .body .effectList li.active{color:#ffd200}.elixirList .body .effectPreview{width:300px;overflow-y:auto}.elixirList .body .effectPreview .effectTooltip{background:none}